.modal-overlay {
  position: fixed;
  display: flex;
  justify-content: center;
  padding-top: 10vh;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal-content {
    max-width: 982px;
    background-color: #fff;
    padding: 40px;
    border-radius: 20px;
    max-height: 83vh;
    overflow: hidden;
}
